import React from "react";
import _ from "lodash";
import { createComponent } from "webiny-app";

class WidgetAction extends React.Component {
    renderTooltip(onClick) {
        const {
            icon,
            tooltip,
            modules: { Tooltip, Icon }
        } = this.props;

        return (
            <Tooltip
                target={
                    <a href="javascript:void(0)" onClick={onClick}>
                        <Icon icon={icon} />
                    </a>
                }
            >
                {tooltip}
            </Tooltip>
        );
    }

    render() {
        const { confirm, onClick = _.noop, onComplete = _.noop, modules: { ClickConfirm } } = this.props;

        if (!confirm) {
            return this.renderTooltip(onClick);
        }

        return (
            <ClickConfirm message={confirm} onComplete={onComplete}>
                {({ showConfirmation }) => this.renderTooltip(() => showConfirmation(onClick))}
            </ClickConfirm>
        );
    }
}

export default createComponent(WidgetAction, { modules: ["Tooltip", "ClickConfirm", "Icon"] });
